import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';

const TrainSearch = () => {
    const [trainNumber, setTrainNumber] = useState('');
    const [trainData, setTrainData] = useState(null);
    const [liveStatus, setLiveStatus] = useState(null);
    const [error, setError] = useState('');
    const [view, setView] = useState('schedule');
    const [intervalId, setIntervalId] = useState(null); // For live status refresh
    const stationsListRef = useRef(null); // For auto-scrolling to current station

    const headers = {
        'x-rapidapi-key': '508baf7622msh132c8f76d4035acp16b0eajsn4740c72bdb98',
        'x-rapidapi-host': 'indian-railway-irctc.p.rapidapi.com',
        'x-rapid-api': 'rapid-api-database',
    };

    const fetchTrainDetails = async () => {
        try {
            const response = await axios.get(
                `https://indian-railway-irctc.p.rapidapi.com/api/trains-search/v1/train/${trainNumber}`,
                {
                    params: { isH5: true, client: 'web' },
                    headers,
                }
            );

            const train = response.data.body[0]?.trains[0];
            if (train) {
                setTrainData(train);
                setLiveStatus(null);
                setError('');
            } else {
                setTrainData(null);
                setError('No train found for this number.');
            }
        } catch (err) {
            setError('Error fetching train data');
            setTrainData(null);
        }
    };

    const fetchLiveStatus = async () => {
        try {
            const today = new Date().toISOString().slice(0, 10);
            const response = await axios.get(
                `https://indian-railway-irctc.p.rapidapi.com/api/trains/v1/train/status`,
                {
                    params: {
                        departure_date: today.replace(/-/g, ''),
                        isH5: true,
                        client: 'web',
                        train_number: trainNumber
                    },
                    headers: headers,
                }
            );

            console.log('Live Status:', response.data);

            const status = response.data.body;
            if (status && Array.isArray(status.stations) && status.stations.length > 0) {
                setLiveStatus(status);
                setTrainData(null);
                setError('');
                // Auto-scroll to current station
                scrollToCurrentStation(status.stations);
            } else {
                setLiveStatus(null);
                setError('No live status found for this train.');
            }
        } catch (err) {
            console.error('Error fetching live train status:', err);
            setError('Error fetching live train status');
            setLiveStatus(null);
        }
    };

    // Auto-scroll to current station
    const scrollToCurrentStation = (stations) => {
        const currentStationIndex = stations.findIndex(station => station.isCurrent);
        if (currentStationIndex !== -1 && stationsListRef.current) {
            const currentStationElement = stationsListRef.current.children[currentStationIndex];
            currentStationElement?.scrollIntoView({ behavior: 'smooth', block: 'center' });
        }
    };

    // Refresh live status every 1 minute
    useEffect(() => {
        if (view === 'live' && liveStatus) {
            const id = setInterval(() => {
                fetchLiveStatus();
            }, 60000); // Refresh every 1 minute
            setIntervalId(id);

            return () => clearInterval(id); // Cleanup interval
        }
    }, [view, liveStatus]);

    return (
        <div style={{ padding: '20px' }}>
            <h2>Train Search</h2>
            <input
                type="text"
                placeholder="Enter Train Number"
                value={trainNumber}
                onChange={(e) => setTrainNumber(e.target.value)}
            />
            <div style={{ margin: '10px 0' }}>
                <button onClick={() => { setView('schedule'); fetchTrainDetails(); }}>Get Schedule</button>
                <button onClick={() => { setView('live'); fetchLiveStatus(); }} style={{ marginLeft: '10px' }}>Live Status</button>
            </div>

            {error && <p style={{ color: 'red' }}>{error}</p>}

            {trainData && view === 'schedule' && (
                <div style={{ marginTop: '20px' }}>
                    <h3>{trainData.trainName} ({trainData.trainNumber})</h3>
                    <p>
                        <strong>From:</strong> {trainData.origin} ({trainData.stationFrom})<br />
                        <strong>To:</strong> {trainData.destination} ({trainData.stationTo})
                    </p>
                    <h4>Schedule:</h4>
                    <ul>
                        {trainData.schedule.map((station) => (
                            <li key={station.stationCode}>
                                <strong>{station.stationName} ({station.stationCode})</strong><br />
                                Arrival: {station.arrivalTime} | Departure: {station.departureTime}<br />
                                Distance: {station.distance} km | Day: {station.dayCount}
                            </li>
                        ))}
                    </ul>
                </div>
            )}

            {liveStatus && view === 'live' && (
                <div style={{ marginTop: '20px' }}>
                    <h3>Live Status</h3>
                    <p><strong>Current Station:</strong> {liveStatus.current_station}</p>
                    <p><strong>Last Update:</strong> {liveStatus.time_of_availability}</p>
                    <p dangerouslySetInnerHTML={{ __html: `<strong>Status:</strong> ${liveStatus.train_status_message}` }} />

                    <h4>Live Schedule:</h4>
                    <ul ref={stationsListRef}>
                        {Array.isArray(liveStatus.stations) && liveStatus.stations.length > 0 ? (
                            liveStatus.stations.map((station, index) => {
                                const isDelayed = station.delay > 0;
                                const isOnTime = !isDelayed;
                                return (
                                    <li key={index} style={{ marginBottom: '10px', transition: 'all 0.3s ease' }}>
                                        <div style={{ display: 'flex', alignItems: 'center' }}>
                                            <strong>
                                                {station.stationName || 'N/A'} ({station.stationCode || 'N/A'})
                                            </strong>
                                            {isOnTime && <span style={{ color: 'green', marginLeft: '10px' }}></span>}
                                            {isDelayed && <span style={{ color: 'red', marginLeft: '10px' }}></span>}
                                        </div>
                                        <br />
                                        Sch Arr: {station.arrivalTime || 'N/A'} | Act Arr: {station.actual_arrival_time || 'N/A'}
                                        <br />
                                        Sch Dep: {station.departureTime || 'N/A'} | Act Dep: {station.actual_departure_time || 'N/A'}
                                        <br />
                                        Platform: {station.expected_platform || 'N/A'} | Halt Time: {station.haltTime || 'N/A'} mins
                                    </li>
                                );
                            })
                        ) : (
                            <p>No station information available.</p>
                        )}
                    </ul>

                </div>
            )}

            {!liveStatus && view === 'live' && !error && (
                <p>No live status found for this train.</p>
            )}
        </div>
    );
};

export default TrainSearch;
